___CSS_LOADER_EXPORT___.push([module.id, `.word-element, .new-container-element {
    display: inline-block;
    text-align: center;
    padding:  5px 10px; 
    height: 40px; /* Задайте высоту вашего элемента */
    line-height: 40px;
    margin: 5px;
    cursor: pointer;
    transition: background-color 0.3s ease;
    background-color: rgb(124, 16, 97);;
    border-radius: 5px;
    color: rgb(251, 251, 252);
    font-size: large;
  }
  .word-element:hover {
    background-color: #1f2191;
  }

  #word-container {
    width: 850px;
    height: 50px;
    display: flex;
    justify-content: stretch;
    align-items: stretch;
  }

.new-container {
  width: 850px;
  height: 300px;
  display: flex;
  gap: 5px;
  height: 60px;
}

.game-container {
  display: flex;
  flex-direction: column-reverse;
  width: 850px;
  height: 750px;
  align-items: stretch;
  justify-content: stretch;
  background-color: rgba(14, 44, 100, 0.5);
  border-radius: 20px;
  padding: 15px;
}

.painting-container {
  width: 850px;
  height: 700px;
}

.continue-button {
  margin: 30px;
  width: 200px;
}

  /* CSS for animation */
  @keyframes moveCard {
    0% {
      transform: scale(1);
    }
    100% {
      transform: scale(0);
    }
  }
  .result-block .word-element {
    animation: moveCard 0.5s ease;

  }

